import { Breadcrumbs, Button, ButtonBase, Card, CardContent, IconButton, List, ListItem, ListItemText, Modal, Paper, Typography } from "@material-ui/core";
import { Link, navigate } from "gatsby";
import * as React from "react";
import Carousel from "react-material-ui-carousel";
import ajax, { useFetcher } from "../../components/ajax";
import { useUserInfo } from "../../components/save";
import { useModalTheme } from "../../components/style";
import Defer from "../../components/ui/Defer";
import Navbar from "../../components/ui/Navbar";
import { expandStrapiImg, rupiah, winconfirm, winprompt } from "../../components/util";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import PinPrompt from "../../components/ui/market/PinPrompt";


const Prompt = ({ inquiry, open, onClose, onAction }) => {
    const modalTheme = useModalTheme();
    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            {open && <Paper className={modalTheme.paper} elevation={5}><List>
                <ListItem>
                    <ListItemText primary={'Alamat'} />
                    <ListItemText primary={inquiry.data.destination?.sector || '-'} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Harga Barang'} />
                    <ListItemText primary={rupiah(inquiry.data.cartPrice)} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Harga Antar'} />
                    <ListItemText primary={rupiah(inquiry.data.deliveryPrice)} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Harga Antar'} />
                    <ListItemText primary={rupiah(inquiry.data.cartPrice + inquiry.data.deliveryPrice)} className={modalTheme.right} primaryTypographyProps={{ variant: 'h6' }} />
                </ListItem>
                <ListItem>
                    <Button onClick={onClose} variant="outlined">Batal</Button>
                    <Button color="primary" variant="contained" style={{ marginLeft: 'auto' }} onClick={onAction}>Beli</Button>
                </ListItem>
            </List></Paper>}
        </Modal>
    );
}

const Page = () => {
    const user = useUserInfo();
    const [rel, setRel] = React.useState(1);
    const list = useFetcher(`/market/cart/?refresh=${rel}`);
    const modalTheme = useModalTheme();
    const [page, setPage] = React.useState(0);
    const [store, setStore] = React.useState(null);
    const checkoutInfo = useFetcher(store ? `/market/cart/checkout?store_id=${store}&refresh=${rel}` : null);
    const edit = (id, qty) => {
        return () => {
            winprompt('Masukkan qty yang baru', qty).then(x => {
                x = parseInt(x);
                if (x && x != qty) {
                    setPage(1);
                    ajax.post('/market/cart/update', {
                        good_id: id,
                        qty: x,
                    }).then(() => {
                        alert('Berhasil disimpan');
                        setRel(rel + 1);
                    }).catch(x => {
                        alert(x.response?.data.message || x.message);
                    }).finally(() => {
                        setPage(0);
                    })
                }
            });
        }
    }
    const remove = (id) => {
        return () => {
            winconfirm('Hapus barang ini?').then(x => {
                if (x) {
                    setPage(1);
                    ajax.post('/market/cart/remove', {
                        good_ids: [id],
                    }).then(() => {
                        alert('Berhasil disimpan');
                        setRel(rel + 1);
                    }).catch(x => {
                        alert(x.response?.data.message || x.message);
                    }).finally(() => {
                        setPage(0);
                    })
                }
            });
        }
    }
    const confirm = (id) => {
        return () => {
            setStore(id);
            setPage(2);
        }
    }
    const checkout = () => {
        setPage(3);
    }
    const realCheckout = (pin) => {
        setPage(4);
        ajax.post('/market/cart/checkout', {
            store_id: store,
            pin,
        }).then(x => {
            alert('Berhasil');
            navigate('/digi/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setRel(rel + 1);
            setPage(0);
        });
    }
    return (
        <Defer data={user && list && page !== 1}>
            {() => <div>
                <Navbar />
                <List>
                    <ListItem>
                        <Breadcrumbs>
                            <Link color="inherit" to="/market/discover/">
                                Belanja
                            </Link>
                            <Typography color="textPrimary">Keranjang</Typography>
                        </Breadcrumbs>
                    </ListItem>
                    {
                        list.data.map(x => <ListItem key={x.id} className={modalTheme.expand}>
                            <Card className={modalTheme.expand}>
                                <List>
                                    <ListItem divider button onClick={() => navigate('/market/store?id=' + x.id)}>
                                        <img src={expandStrapiImg(x.store.logo, 'thumbnail')} width="40px" style={{ marginRight: '10px' }} />
                                        <ListItemText primary={x.store.title} />
                                    </ListItem>
                                    {x.cart.map(y => <ListItem key={y.id} >
                                        <ButtonBase onClick={() => navigate('/market/good?id=' + y.good.id)}>
                                            <img src={expandStrapiImg(y.good.galleries[0], 'thumbnail')} width="60px" style={{ marginRight: '10px' }} />
                                            <ListItemText primary={y.good.title} secondary={`${rupiah(y.good.price)} x ${y.qty}`} />
                                        </ButtonBase>
                                        <IconButton className={modalTheme.right} onClick={edit(y.good.id, y.qty)}>
                                            <EditIcon />
                                        </IconButton>
                                        <IconButton onClick={remove(y.good.id)}>
                                            <DeleteIcon />
                                        </IconButton>
                                    </ListItem>)}
                                    <ListItem divider />
                                    <ListItem>
                                        <Button onClick={confirm(x.store.id)} >Checkout</Button>
                                    </ListItem>
                                </List>
                            </Card>
                        </ListItem>)
                    }
                    {
                        list.data.length === 0 && <ListItem>
                            <ListItemText primary="Belum ada produk masuk ke keranjang" />
                        </ListItem>
                    }
                </List>
                <Prompt open={page === 2 && checkoutInfo} onClose={() => setPage(0)} onAction={checkout} inquiry={checkoutInfo} />
                <PinPrompt open={page >= 3} submitting={page === 4} onClose={() => setPage(0)} onAction={realCheckout} />
            </div>
            }
        </Defer>
    );
}

export default Page
